import React from 'react'
import Link from 'gatsby-link'
import {withPrefix} from 'gatsby-link'
import ScrollAnimation from 'react-animate-on-scroll'
import Img from 'gatsby-image'
import westerner from '../images/westerner.png'
import mountain from '../images/mountain.png'
import leaf from '../images/leaf.png'
import target from '../images/target.png'
import FontAwesome from 'react-fontawesome'
import Slider from "react-slick"
import Portfolio from '../components/Portfolio'


const IndexPage = () => (
  <div>
    <div id="homeIntro" className="fullpage">
          <div className="mountainwrapper">   
            <ScrollAnimation animateIn="slideInLeft" offset="1" >   
                <img id="mountain" src={mountain}/>      
            </ScrollAnimation>
          </div>
          <div className="westernerwrapper">
            <ScrollAnimation animateIn="slideInRight" offset="0" >        
                <img id="westerner" src={westerner}/>        
            </ScrollAnimation>
          </div>
          <div className="leaf">     
                <img src={leaf}/>
                <img src={leaf}/>         
          </div>
          <div className="targetwrapper">
            <ScrollAnimation animateIn="slideInRight" offset="0" >  
                <img id='target' src={target}/>
            </ScrollAnimation>
          </div>         
          <div id="myname">
            <ScrollAnimation animateIn="fadeIn" animateOnce>
            <h1>Rocky Yip</h1>
            <h2><FontAwesome name='flag' />Front End Developer<FontAwesome name='flag' /></h2>
            </ScrollAnimation>  
          </div>
        <div id="introText">
            <ScrollAnimation animateIn="fadeIn" animateOnce>
            <h3 className="quote">Always Aiming To Achieve</h3>
            </ScrollAnimation>
               
        </div>

    </div>
    <div id="aboutMe" className="fullpage">
        <div className="container">
            <ScrollAnimation animateIn="fadeIn">
            <h2>About Me</h2>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeIn">
                <div class="row">
                    <div className='col-md-12 text-center'>
                    <FontAwesome className='bigIcon' name='user-circle'/>
                    </div>
                </div>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeIn"> 
                <div className='row'>
                    <div className="col-md-8 offset-md-2">
                        <p>I'm an experienced front end web developer located in beautiful Vancouver, BC. I am very passionate about web technologies and development. </p>
                        <p>Front end web technologies have experienced dramatic changes in the past 5 years. Shifting from a Flash developer to well-rounded web developer has provided me with opportunities to work with businesses of different industiries such as HSBC Canada, LifeLabs Medical Laboratories, Invoke Media and Sun Life Financial.</p>
                        <p>I'm also an artist, UI/Game/Graphic designer, game developer and gamer.</p>
                    </div>

                </div>
            </ScrollAnimation>   
             
        </div>
    </div>
    <div id="works" className="fullpage">
        <div className="container">
            <ScrollAnimation animateIn="fadeIn"><h2>My Works</h2></ScrollAnimation>
            <div className='row'>
                
                    <div className="col-md-4 col-sm-12 workboxWrapper">
                        <ScrollAnimation animateIn="fadeIn">
                            <Portfolio name="lifelabs.com (screenshot coming soon)"></Portfolio>
                            <p className="workDesc">LifeLabs.com is the main corporate site of Canada's largest medical laboratories.</p>
                            <p className="workRole">Full-stack Lead Developer</p>
                            <p className="workEnv">HTML5, CSS, Javascript, JQuery, Bootstrap, SharePoint, SPServices, C#</p>
                        </ScrollAnimation>
                    </div>
                    <div className="col-md-4 col-sm-12 workboxWrapper">
                        <ScrollAnimation animateIn="fadeIn">
                            <Portfolio name="EMD (screenshot coming soon)"></Portfolio>
                            <p className="workDesc">EMD Employee Exam Generator is a CRUD based intranet application for the business' exam administrator to enter exam questions and generate web and paper-based exams.</p>
                            <p className="workRole">Full-stack Developer</p>
                            <p className="workEnv">HTML, CSS/LESS/SASS, Javascript, JQuery, Bootstrap, Angular</p>
                        </ScrollAnimation>
                    </div>
                    <div className="col-md-4 col-sm-12 workboxWrapper">
                        <ScrollAnimation animateIn="fadeIn">
                            <Portfolio name="SuperJumbo  (screenshot coming soon)"></Portfolio>
                            <p className="workDesc">SuperJumbo Media is my personal multimedia development business. I have worked with businesses across various industries creating unique and custom web-based solutions to meet the clients' business needs.</p>
                            <p className="workRole">Full-stack Developer/Founder</p>
                            <p className="workEnv">HTML, CSS/LESS/SASS, Javascript/ES6, JQuery, Bootstrap, SharePoint, PHP, iOS, Android, AngularJS/Angular, React</p>
                        </ScrollAnimation>
                    </div>
                
            </div>
            <div className='row'>
            <div className="col-md-4 col-sm-12 workboxWrapper">
                        <ScrollAnimation animateIn="fadeIn">
                            <Portfolio name="APEX Diagnostic (screenshot coming soon)"></Portfolio>
                            <p className="workDesc">APEX Diagnostic approached me for a quick web solution for their organization. WordPress was chosen to allow content owner to easily maintain the site content.</p>
                            <p className="workRole">Front End Developer</p>
                            <p className="workEnv">WordPress, HTML/CSS, JQuery</p>
                        </ScrollAnimation>
                    </div>
                    <div className="col-md-4 col-sm-12 workboxWrapper">
                        <ScrollAnimation animateIn="fadeIn">
                            <Portfolio name="Movie Biz Tycoon (screenshot coming soon)"></Portfolio>
                            <p className="workDesc">Movie Biz Tycoon is a business simulation game for the iOS.</p>
                            <p className="workRole">Owner/Developer</p>
                            <p className="workEnv">Lua Script, Corona SDK, Photoshop</p>
                        </ScrollAnimation>
                    </div>
                    
                
            </div>
        </div>
    </div>
    <div id="tools" className="fullpage">
        <div className="container">
            <ScrollAnimation animateIn="fadeIn"><h2>My Tools</h2></ScrollAnimation>
            <ScrollAnimation animateIn="fadeIn">
                <p className="text-center">Here are some of my tools of trade:</p>
            </ScrollAnimation>
            <Slider>
                <div className="text-center">
                    <h3>Front End</h3>
                    <div className="row">
                        <div className="col-md-4 offset-md-2"> 
                            <ul>
                                <li>Javascript/ES6</li>
                                <li>jQuery</li>
                                <li>Bootstrap</li>
                                <li>AngularJS/Angular</li>
                                <li>React</li>
                                <li>HTML</li>
                                <li>CSS/SASS</li>
                                <li>Google Map API</li>
                            </ul>
                        </div>
                    </div>
                </div>
                <div className="text-center">
                    <h3>Other</h3>
                    <ul>
                        <li>Webpack</li>
                        <li>Git</li>
                    </ul>
                </div>
            </Slider>
        </div>
    </div>
    <div id="contact" className="fullpage">
        <div className="container">
            <ScrollAnimation animateIn="fadeIn"><h2>Contact Me</h2></ScrollAnimation>
            <div className='row'>
                    <div className="col-md-8 offset-md-2">
                        <p className="text-center"><a href="mailto:rockydesign@gmail.com">rockydesign@gmail.com</a></p>
                        {//<form>
                            //<label>Name:</label>
                            //<input type='text' className='form-control'/>
                            //<label>Message:</label>
                            //<textarea className='form-control'></textarea>
                        //</form>
                        }
                    </div>
            </div>
        </div>
    </div>
  </div>
)

export default IndexPage



